html, body, .content {
    height: 100%;
    font-family: 'Montserrat', sans-serif;
  }

h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
    letter-spacing: .1rem;
}

p, a {
    font-weight: 400;
    letter-spacing: .2rem;
}

h1 {
    font-size: 2.2rem;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
}

h2, h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

p, a {
    font-size: 1.2rem;
    margin-bottom: .5rem;
}